.transactionTable {
  margin-top: 50px;
  border-radius: 10px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);

  overflow: hidden;
}

.tableHeader {
  background-color: orange;
}

.tableColumn {
  width: 500px;
  padding: 10px;
  border-radius: 5px;
  font-weight: 600;
  text-transform: uppercase;
}
